<template>
  <div class="AnalysisIndexPage pa-3">
    <div v-if="checkAllDataReady">
      <div class="d-flex justify-space-between align-center">
        <h1>年度營業額統計表</h1>
        <v-spacer></v-spacer>
        <v-btn 
          v-if="formData.length"
          class="success" 
          @click="showChedkedChart()"
        >顯示勾選資料圖表</v-btn>
        <v-switch 
          :label="showSeason ? '顯示月' : '顯示季'" 
          v-model="showSeason" 
          v-if="formData.length"
          class="ml-2"
        ></v-switch>
        <v-text-field
          type="number"
          label="選擇年份"
          v-model="selectYear"
          class="ml-6"
        ></v-text-field>
        <v-btn 
          class="ml-2" small 
          @click="buildFormData()" 
          :loading="loading" :disabled="loading"
        >顯示指定年份資料</v-btn>
      </div>

      <v-divider class="my-2"></v-divider>

      <!-- 顯示月 -->
      <v-simple-table v-if="!showSeason"
       fixed-header  height="77vh">
        <template v-slot:default>
          <thead class="text-no-wrap">
            <tr class="setBgColor-2 fontColor-4">
              <th class="text-center text-h6 font-weight-bold">
                <!-- 選擇 -->
                <v-checkbox 
                v-if="formData.length"
                :label="selectAll ? '全不選' : '全選'" v-model="selectAll" @click="selectSwitch()"
                ></v-checkbox>
              </th>
              <th class="text-center text-h6 font-weight-bold">客戶名稱</th>
              <th 
                v-for="index in 12" :key="index"
                class="text-center text-h6 font-weight-bold" 
                >
                {{ index }}月
              </th>
              <th class="text-center text-h6 font-weight-bold">年度總金額</th>
            </tr>
          </thead>
          <tbody class="text-right">
            <tr 
              v-for="(item, index) in formData" :key="index" 
              @click.stop="showChart(item)"
              :class="index == 0 ? 'setBgColor-1' : ''"
              >
              <td><v-checkbox v-model="item.checked" @click.stop></v-checkbox></td>
              <td class="text-center" :class="index == 0 ? 'text-h6 font-weight-bold' : ''">{{ item.name }}</td>
              <td v-for="(item,monthIndex) in item.monthData" :key="monthIndex" :class="index == 0 ? 'font-weight-bold' : ''">
                {{ item.subTotal | filtermoney }}
              </td>

              <td>
                {{ item.yearData.subTotal | filtermoney }}
              </td>
            </tr>

          </tbody>
        </template>
      </v-simple-table>

      <!-- 顯示季 -->
      <v-simple-table v-else
       fixed-header height="77vh">
        <template v-slot:default>
          <thead class="text-no-wrap">
            <tr class="setBgColor-2 fontColor-4">
              <th class="text-center text-h6 font-weight-bold">客戶名稱</th>
              <th 
                v-for="index in 4" :key="index"
                class="text-center text-h6 font-weight-bold" 
                >
                {{ (index - 1)*3 + 1 }} ~ {{ (index - 1)*3 + 3 }}月
              </th>
              <th class="text-center text-h6 font-weight-bold">年度總金額</th>
            </tr>
          </thead>
          <tbody class="text-right">
            <tr v-for="(item, index) in formData" :key="index" :class="index == 0 ? 'setBgColor-1' : ''">
              <td class="text-center" :class="index == 0 ? 'text-h6 font-weight-bold' : ''">{{ item.name }}</td>
              <td v-for="sIndex in 4" :key="sIndex" class="text-center" :class="index == 0 ? 'font-weight-bold' : ''">
                {{ returnSeasonTotal(sIndex,item.monthData) | filtermoney }}
              </td>

              <td class="text-center" :class="index == 0 ? 'font-weight-bold' : ''">
                {{ item.yearData.subTotal | filtermoney }}
              </td>
            </tr>

          </tbody>
        </template>
      </v-simple-table>


    </div>
    <div v-else>
      <h2>資料處理中...</h2>
    </div>

    <ChartPage ref="childFn" />
  </div>
</template>

<script>
import dayjs from "dayjs";
import isBetween from "dayjs/plugin/isBetween";
dayjs.extend(isBetween);

import ChartPage from './ChartPage.vue'

export default {
  components:{ ChartPage },
  data() {
    return {
      shipmentItems: null, //對帳單資料
      paymentItems: null, //應收帳款資料
      selectYear: dayjs().format("YYYY"),

      formData:[],  //實際輸出表格內容
      loading: false, //資料處理中..

      showSeason: false, //顯示年/季

      selectAll: false, //選擇全選/全不選切換
    };
  },

  filters: {
    filtermoney(num) {
      if (!num) {
        return "";
      }
      const n = Number(num);
      return `$${n.toFixed(0).replace(/./g, (c, i, a) => {
        const currency =
          i && c !== "." && (a.length - i) % 3 === 0
            ? `, ${c}`.replace(/\s/g, "")
            : c;
        return currency;
      })}`;
    },
  },

  computed: {
    //判斷需要的資料是否都已讀入
    checkAllDataReady() {
      if (this.shipmentItems != null) {
        return true;
      } else {
        return false;
      }
    },
  },

  mounted() {
    this.getData("shipment");
  },

  methods: {
    //全選/全不選
    selectSwitch(){
      this.formData.forEach(i=>{
        if (this.selectAll){
          i.checked = true
        }else{
          i.checked = false
        }
      })
    },
    // 顯示已勾選的資料的圖表
    showChedkedChart(){
      let match = this.formData.filter(i=>i.checked)
      if (match.length == 0){
        this.$swal({ icon: "warning", text: "未勾選任何資料" });
      }else{
        //顯示圖表
        this.$refs.childFn.showMultiProcess(match)
      }
    },
    //顯示圖表
    showChart(item){
      // console.log(item)
      this.$refs.childFn.showProcess(item)
    },
    //回傳各筆資料的年度總合
    returnYearTotal(dataItems){
      return dataItems.reduce((total,item)=>{
        return total = Number(total) + item.yearData.subTotal
      },0)
    },
    //回傳每月總合;資料來源
    returnMonthTotal(index,dataItems){
      return dataItems.reduce((total,item)=>{
        return total = Number(total) + item.monthData[index-1].subTotal
      },0)
    },
    //回傳季總合,index為第幾季,item為1-12月的資料
    returnSeasonTotal(index,item){
      // console.log(item)
      let total = 0
      let min = (index - 1)*3
      let max = (index - 1)*3 + 2
      for (min;min<=max;min++){
        total = total + Number(item[min].subTotal)
      }
      return total;
    },
    //建構表格內各項資料
    buildFormData() {
      this.loading = true
      this.$store.commit("setLoading", true); //讀取畫面on

      //1.取得.符合今年時間區段的資料
      let yearFirstDay = dayjs().format(`${this.selectYear}-01-01`);
      let yearLastDay = dayjs().format(`${this.selectYear}-12-31`);

      let matchYear = this.shipmentItems.filter((i) =>dayjs(i.date).isBetween(yearFirstDay,yearLastDay,null,"[]"));

      //計算出1.的資料中的各項資料總合
      matchYear = matchYear.map((i) => {
        return {
          ...i,
          totalPrice: this.priceTotal(i), //買賣金額
          punchlockPrice: this.optionTotal(
            i,
            "punchlock",
            "extraPunchlockPrice"
          ), //沖鎖
          foldPrice: this.optionTotal(i, "fold", "extraFoldPrice"), //折工
          machiningPrice: this.optionTotal(
            i,
            "machining",
            "extraMachiningPrice"
          ), //加工
        };
      });

      //建構1~12月的每月起始跟結束日期陣列-備用
      let monthArr = []
      for (let month = 1;month<=12; month++){
        let monthStartDay = dayjs().format(`${this.selectYear}-${month}-01`);
        let theMonthLastDay = dayjs(`${this.selectYear}-${month}`).daysInMonth();
        let monthEndDay = dayjs(month).format(`${this.selectYear}-${month}-${theMonthLastDay}`);

        monthArr.push({
          monthStartDay:monthStartDay,
          monthEndDay:monthEndDay
        })
      }  

      //2.取得所有符合資料的顧客snkey -> 取得不重覆陣列值;改成1~12月每個月的陣列資料;加1~12月總計
      let snkeyItems = matchYear.map((i) => i.customer.snkey); //取得所有符合資料的顧客snkey
      snkeyItems = [...new Set(snkeyItems)]; //取得不重覆陣列值(客戶資料)

      let tableDatas = snkeyItems.map((snkey) => {  //建構表格資料 1~12月個別月份的四項總合及年度總合;回傳值為obj
        let matchArr = matchYear.filter((i) => i.customer.snkey == snkey);  //取得指定客戶的資料

        let dataArr = {  //最後每列回傳的資料;預設值為客戶code及name
          code: matchArr[0].customer.code,
          name: matchArr[0].customer.name
        };

        //建構年度總金額資料
        dataArr.yearData = this.returnTotalArr(matchArr);
        

        //建構1~12個月的資料
        dataArr.monthData = []
        monthArr.forEach((days)=>{
          let matchMonth = matchArr.filter(i=>
            dayjs(i.date).isBetween(dayjs(days.monthStartDay),dayjs(days.monthEndDay),null,"[]")
          )
          dataArr.monthData.push(this.returnTotalArr(matchMonth))
        })

        return dataArr
      });

      //建構每月總合 及年度總合
      let monthTotalArr = {
        name:'總合',
        monthData:[],
        yearData:{
          subTotal: this.returnYearTotal(tableDatas)
        }
      }
      for (let i=1;i<=12;i++){
        monthTotalArr.monthData.push({
          subTotal: this.returnMonthTotal(i,tableDatas)
        })
      }

      // console.log(tableDatas,monthTotalArr)
      tableDatas.unshift(monthTotalArr)

      //加入勾選方塊
      tableDatas = tableDatas.map(i=>{
        return {
          ...i,
          checked: false
        }
      })

      this.loading = false;
      this.$store.commit("setLoading", false); //讀取畫面on
      this.formData = tableDatas;
    },
    /*
      回傳指定資料的四個總合值
      allTotalPrice 買賣金額;allPunchlockPrice 沖鎖金額; allFoldPrice 折工金額;allMachiningPrice 加工金額
    */
    returnTotalArr(items){
      let obj = {
        allTotalPrice: this.comTotal(items, "totalPrice"),
        allPunchlockPrice: this.comTotal(items, "punchlockPrice"),
        allFoldPrice: this.comTotal(items, "foldPrice"),
        allMachiningPrice: this.comTotal(items, "machiningPrice"),
      }
      obj.subTotal = Object.values(obj).reduce((total,i)=>{
        return total = Number(total) + Number(i)
      },0);

      return obj;
    },
    changeState(item, type) {
      if (item.paymentState[type]) {
        item.paymentState[type + "Date"] = dayjs().format("YYYY-MM-DD");
      } else {
        item.paymentState[type + "Date"] = "";
      }
      let postData = {
        snkey: item.paymentState.snkey,
        datalist: JSON.stringify(item.paymentState),
      };

      this.$api.post("/payment",postData)
      .then((rs) => {
        // console.log(rs);
      });
    },
    //取得所有應收帳款資料
    getData(dataTable) {
      this.$api.get(dataTable)
      .then((rs) => {
        this[dataTable + "Items"] = rs.map((i) => {
          let newItem = JSON.parse(i.datalist);
          newItem.snkey = i.snkey;
          if (newItem.picname == undefined) {
            newItem.picname = "lazypic.jpg";
          }
          return newItem;
        });
      });
    },
    //回傳單筆資料總額
    priceTotal(items) {
      return items.tempItems.reduce((total, item) => {
        return (total += this.filterFinalPrice(item));
      }, 0);
    },
    filterFinalPrice(item) {
      if (item.noSize) {
        var val =
          item.quantity *
          (parseFloat(item.rawmaterial.price) +
            parseFloat(item.extraRawmaterialPrice));
      } else {
        var val =
          item.weight *
          (parseFloat(item.rawmaterial.price) +
            parseFloat(item.extraRawmaterialPrice));
      }
      return Math.round(val); //2021.3.19 改回傳四捨五入,取整數
      // return this.roundUp(val, 0);
    },
    //各種工作總金額
    optionTotal(items, type, extraType) {
      return items.tempItems.reduce((total, item) => {
        if (item[type]) {
          return (total +=
            (parseFloat(item[type].price) + parseFloat(item[extraType])) *
            item.quantity);
        } else {
          return total;
        }
      }, 0);
    },
    //回傳指定項目的總合
    comTotal(items, type) {
      return items.reduce((total, i) => {
        return (total += parseInt(i[type]));
      }, 0);
    },
  },
};
</script>