<template>
    <div>
        <v-dialog v-model="dialog">
            <v-card v-if="dialog && chartData != null">
                <div class="bannerStyle text-center">
                    {{ name }}
                </div>
                <v-divider></v-divider>
                <VeLine :data="chartData" ></VeLine>

            </v-card>
        </v-dialog>
    </div>
</template>

<script>
import VeLine from 'v-charts/lib/line.common'  //曲線圖
import VeHistogram from 'v-charts/lib/histogram.common'  //長條圖

export default {
    components:{ VeLine, VeHistogram },
    data(){
        return {
            dialog:false,
            items:null,
            chartData:null,

            name:"",
        }
    },

    methods:{
        //多筆顯示
        showMultiProcess(items){
            this.items = items;
            this.dialog = true;
            this.$nextTick(()=>{
                let dataItems = []
                let colums = ['月份']
                //建構月份
                for (let i=1;i<=12;i++){
                    dataItems.push({
                        '月份': `${i}月`
                    })
                }

                items.forEach(item=>{
                    colums.push(item.name)
                    item.monthData.forEach((mItem,index)=>{
                        dataItems[index][item.name] = mItem.subTotal
                    })
                })
                
                this.name = '多筆比較: ' + colums.slice(1)
                this.chartData = {
                    columns: colums,
                    rows: dataItems
                }
            })
        },
        //單筆顯示
        showProcess(items){
            this.items = items;
            this.name = items.name;
            this.dialog = true;
            this.$nextTick(()=>{
                this.chartData = {
                    columns: ["月份", "營業額"],
                    rows: items.monthData.map((item,index)=>{
                        return {
                            '月份' : `${index+1}月` ,
                            '營業額' : item.subTotal,
                        }
                    })
                }
            })

            
        }
    }
}
</script>